@import "../../../../css-config/mixins.scss";
.categories{
    display: grid;
    grid-template-columns: auto auto;
    
   
    gap: 10px;

    @include md{ 
        grid-template-columns: auto auto auto auto;

    }
    .category{
        background-color: black;
        
        overflow: hidden;
        cursor: pointer;
     

       
        
        img{
            width: 100%;
            height: 100%;
           
            display: block;
            transition: all ease 0.3s;
            
            

            &:hover{
              
                    transform: scale(1.2);
               
            }

           

        }
