.main-slider{padding:30px 40px}.main-slider .content{display:flex;justify-content:center;align-items:center;flex-direction:column;gap:10px}.main-slider .content .image-slider{width:100px;height:100px;border-radius:100%;border:1px solid #2b2828;padding:6px}.main-slider .content .heading{font-size:17px;color:brown}.main-slider .react-multi-carousel-list{display:flex;align-items:center;overflow:hidden;position:relative}.main-slider .react-multi-carousel-track{list-style:none;padding:0;margin:0;display:flex;flex-direction:row;position:relative;transform-style:preserve-3d;backface-visibility:hidden;will-change:transform,transition}.main-slider .react-multiple-carousel__arrow{position:absolute;outline:0;transition:all .5s;border-radius:35px;z-index:1;border:0;background:#add8e6;min-width:43px;min-height:43px;opacity:1;cursor:pointer}.main-slider .react-multiple-carousel__arrow:hover{background:rgba(0,0,0,.5)}.main-slider .react-multiple-carousel__arrow::before{font-size:20px;color:gray;display:block;font-family:revicons;text-align:center;z-index:2;position:relative}.main-slider .react-multiple-carousel__arrow:disabled{cursor:default;background:rgba(0,0,0,.5)}.main-slider .react-multiple-carousel__arrow--left{left:calc(4% + 1px)}.main-slider .react-multiple-carousel__arrow--left::before{content:""}.main-slider .react-multiple-carousel__arrow--right{right:calc(4% + 1px)}.main-slider .react-multiple-carousel__arrow--right::before{content:""}.main-slider .react-multi-carousel-dot-list{position:absolute;bottom:0;display:flex;left:0;right:0;justify-content:center;margin:auto;padding:0;margin:0;list-style:none;text-align:center}.main-slider .react-multi-carousel-dot button{display:inline-block;width:12px;height:12px;border-radius:50%;opacity:1;padding:5px 5px 5px 5px;box-shadow:none;transition:background .5s;border-width:2px;border-style:solid;border-color:gray;padding:0;margin:0;margin-right:6px;outline:0;cursor:pointer}.main-slider .react-multi-carousel-dot button:hover:active{background:#080808}.main-slider .react-multi-carousel-dot--active button{background:#080808}.main-slider .react-multi-carousel-item{transform-style:preserve-3d;backface-visibility:hidden}@media all and (-ms-high-contrast: none),(-ms-high-contrast: active){.main-slider .react-multi-carousel-item{flex-shrink:0 !important}.main-slider .react-multi-carousel-track{overflow:visible !important}}.main-slider [dir=rtl].react-multi-carousel-list{direction:rtl}.main-slider .rtl.react-multiple-carousel__arrow--right{right:auto;left:calc(4% + 1px)}.main-slider .rtl.react-multiple-carousel__arrow--right::before{content:""}.main-slider .rtl.react-multiple-carousel__arrow--left{left:auto;right:calc(4% + 1px)}.main-slider .rtl.react-multiple-carousel__arrow--left::before{content:""}