.flex1{
display: flex;


.firstr{
  margin-top: 25px;
  margin: 0 10px;
  width: 700px;
  border: 1px solid lightblue;
  background-color:#ffffff;
  padding: 10px;
  height: fit-content;
  border-radius: 5px;

  @include md {
      margin-top: 50px;
      // margin: 0 10px;

  }
  .heading{
    font-size: 18px;
    font-weight: 700;
    margin-bottom: 10px;
  }
  .heading2{
    font-size: 18px;
    font-weight: 700;
    margin: 10px 0;
  
  }
  .go{
    padding: 10px;
    margin: 0 5px;
    background-color: lightblue;
    color: black;
    border: none;
    font-size: 16px;
  }
  .go:hover{
    background-color: darkblue;
    color: #ffffff;
  }
}


.aategiryperpage {
    margin: 25px;

    @include md {
        margin: 50px;
    }

    .cat-heading {
        margin-bottom: 20px;
        font-size: 18px;
        font-weight: 500;
        text-transform: uppercase;

        @include md {
            margin-bottom: 35px;
            font-size: 24px;
        }

        &:after {
            content: "";
            display: block;
            margin-top: 5px;
            width: 50px;
            height: 3px;
            // background-color: #8e2de2;
            background-color: lightblue;

            @include md {
                margin-top: 10px;
            }
        }
    }

    .cat {
        display: grid;


        grid-template-columns: auto auto;

        // padding: 0 20px;
        gap: 80px 30px;
        margin-bottom: 80px;
        margin-top: 20px;


        @include md {
            grid-template-columns: auto auto auto ;

        }

        .imgtr {
            // background-color: black;
            background-color: lightblue;
        
            overflow: hidden;
            cursor: pointer;


            img {
                width: 100%;
                height: 100%;
                overflow: hidden;
                display: block;
                transition: all ease 0.3s;


                &:hover {

                    transform: scale(1.2);

                }

            }
        }

          .heading_catimg{
            margin-top: 25px;
            margin-bottom: 5px;
            color: rgb(65, 64, 64);
           
            font-size: 18px;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            width: 250px;
            font-family: Arial, Helvetica, sans-serif;
           
          
          }

          .price1{
            color: rgb(20, 20, 20);
            
            font-size: 25px;
          }
          .delprice{
            color: gray;
            padding: 0 4px;
            font-size: 18px;
          }
          .discount{
            font-size: 16px;
          }
          .rupee{
            font-size: 15px;
       
          }
          .save{
            color: gray;
          }
        }





    }


  }