@import "../../css-config/mixins.scss";


.main-menu{
    position: fixed;
    top: 0;
    right: 0;
    width: 100%;
    height: 100%;
    display: flex;
    
    justify-content: flex-end;
    z-index: 999;
    overflow: hidden;

    .menu-layer{
        background-color: rgba(0, 0, 0, 0.5);
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
    }

    .menu-content{
        width: 100%;
        height: 100%;
        background: white;
        position: relative;
     
        z-index: 1;
        display: flex;
        flex-direction: column;
        // transform: translateX(100%);
        animation: slideCartWindow 0.3s ease forwards;
        @include md{
          width: 25%;

        }

        .menu-row{
            display: flex;
            justify-content: space-between;
            padding: 15px 20px;
            background-color: lightblue;
            height: 80px;
            width: 100%;
            align-items: center;
            color: gray;
            font-size: 18px;
            
        }
        .menu-text{
            padding: 0 10px;
        }
        .close-btnn{
            font-size: 25px;

        }
        .menu-heading{
            padding: 10px 20px;
            font-size: 20px;
            color:rgb(17, 17, 17) ;
        }
        .list-menu{
         
            font-size: 18px;
            color: gray;
            list-style: none;

        }
        .list-menu li{
            padding: 15px 20px;
        }
        .list-menu li:hover{
            background-color: rgb(201, 201, 221)
        }
    }
};