@import "../../css-config/mixins.scss";

.single-product-main-content {
    margin: 20px 0;

    @include md {
        margin: 75px 0;
    }

    .layout {
        max-width: calc(100% - 20px);
        margin: 0 auto;

        @include md {
            max-width: 1200px;
        }

        .single-product-page {
            display: flex;
            flex-direction: column;

            @include md {
                flex-direction: row;
                gap: 100px;
            }

            .left {
                width: 100%;
                // background-color: rgba(0,0,0,0.05);
                flex-shrink: 0;

                @include md {
                    width: 500px;
                    height: 500px;


                }

                img {
                    width: 100%;
                    display: block;
                }


            }

            .right {
                display: flex;
                flex-direction: column;
                padding-top: 20px;

                @include md {
                    padding: 0 35px;

                }

                .name {
                    font-size: 16px;
                    line-height: 28px;
                    margin-bottom: 20px;

                    @include md {
                        font-size: 24px;
                        line-height: 32px;
                    }
                }

                .price {
                    font-size: 20px;
                    line-height: 32px;
                    margin-bottom: 20px;
                }



                .delprice {
                    color: gray;
                    padding: 0 4px;
                    font-size: 18px;
                }

                .discount {
                    font-size: 16px;
                }

                .rupee {
                    font-size: 15px;

                }

                .bed_text {
                    color: gray;
                    font-size: 15px;
                }


                .desc {
                    font-size: 14px;
                    line-height: 20px;
                    margin-bottom: 20px;
                    color: #6b6b6b;

                    @include md {
                        font-size: 16px;
                        line-height: 24px;
                    }

                    .option {
                        padding: 5px;
                        margin: 5px;

                    }

                    .option1 {
                        padding: 5px;
                    }
                }

                .cart-buttons {
                    display: flex;

                    @include md {
                        margin-top: 10px;
                    }

                    .quantity-buttons {
                        width: fit-content;
                        display: flex;
                        border: 2px solid rgba(0, 0, 0, 0.2);
                        margin-right: 10px;
                        height: 50px;

                        span {
                            font-size: 18px;
                            width: 40px;
                            display: flex;
                            align-items: center;
                            justify-content: center;
                            cursor: pointer;
                            color: #6b6b6b;

                            &:nth-child(1) {
                                border-right: 2px solid rgba(0, 0, 0, 0.2);
                            }

                            &:nth-child(2) {
                                width: 60px;
                            }

                            &:nth-child(3) {
                                border-left: 2px solid rgba(0, 0, 0, 0.2);
                            }
                        }
                    }

                    .add-to-cart-button {
                        outline: 0;
                        border: 0;
                        height: 50px;
                        width: 180px;
                        display: flex;
                        align-items: center;
                        justify-content: center;
                        cursor: pointer;
                        background: lightblue;
                        color: rgb(22, 22, 22);
                        font-size: 16px;
                        border-bottom: 3px solid #242225;
                        flex-grow: 1;
                        border-radius: 10px;

                        :hover {
                            background-color: aqua;
                        }

                        @include md {
                            flex-grow: unset;
                        }
                    }
                }

                .divider {
                    margin: 20px;
                    height: 3px;
                    width: 100%;
                    background-color: rgba(0, 0, 0, 0.1);
                }


                .info-item {
                    display: flex;
                    gap: 100px;

                    .text-bold {
                        font-size: 18px;
                        font-weight: 500;
                        display: block;

                        &:nth-child(1) {
                            margin-bottom: 20px;
                        }

                        span {
                            font-size: 16px;
                            font-weight: 400;
                            cursor: pointer;
                            color: #6b6b6b;

                        }

                        svg {
                            margin: 0 5px;
                            transition: all ease-in-out;
                        }

                        svg:hover {
                            transform: scale(1.5);
                            animation: 0.2s;
                        }
                    }
                }



            }
        }

        .flex {

            .description {
                font-size: 20px;
                color: black;


                border-bottom: 4px solid lightblue;
                padding: 10;

            }

            .accordian {
                margin-top: 20px;
            }

            .accor {
              font-size: 18px;
              font-weight: 700;


            }

            .rows {
                display: flex;

                justify-content: space-between;
            }

            .firstrow {
                color: #5b5b5b;
                font-weight: 700;
            }

            .secondrow {
                padding-bottom: 10px;
                color: #848484;
                font-weight: 500;
            }

           
        }
    }

   
}